<template>
  <div id="container">
    <HeaderUserBar />

    <div id="market">
      <div class="mt-title">Ticket Market</div>
      <div class="mt-tgs">
        <lottie-player
            autoplay
            loop
            mode="normal"
            style="width: 130px"
        ></lottie-player>
      </div>
      <div class="mt-soon">{{ $store.state.lang === 'ru' ? 'Скоро...' : 'Tez kunda...' }}</div>
    </div>

    <BottomNavigationMenu active="market" :lang="$store.state.lang === 'ru'" />
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import TimeAnimation from "@/files/735482909485738.json";

export default {
  name: "Market",
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  mounted() {
    document.title = 'Ticket Market';
    const player = document.querySelector("lottie-player");
    player.addEventListener("rendered", (e) => {
      //Load via URL
      player.load(TimeAnimation);
    });
  },
}
</script>
<style scoped>
lottie-player {
  margin-top: 30px;
  width: 130px;
  height: 130px;
}
</style>
